@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&family=Open+Sans&family=Raleway:wght@600&display=swap');
@import url('http://fonts.cdnfonts.com/css/fifa-welcome');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  width: 100%;
  font-family: 'Montserrat', sans-serif;
  font-family: 'Open Sans', sans-serif;
  font-family: 'Raleway', sans-serif;
  font-size: 20px;
  color: aliceblue;
  background-image: linear-gradient(145deg, #03e9f4, #03ef94);
  background-color: #151515;
  background-blend-mode: soft-light;
}
.search-page {
  width: 100%;
  height: 100vh;
  padding: 1em;
  background-image: url(./FIFA-21-Titel-Update-8-Patch-Notes.jpg);
  background-size: cover;
}
.search-box {
  width: 380px;
  height: 50px;
  border-radius: 5px;
  overflow: hidden;
}

.search-box-container {
  background: #184063;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2em;
  width: 500px;
}

.search-box-container h1 {
  font-family: 'FIFA Welcome', sans-serif;
  font-size: 2.8rem;
}

.search-box input {
  height: 100%;
  width: 75%;
  border: none;
  outline: none;
  font-family: 'Open Sans', sans-serif;
  font-size: 1.2rem;
  box-shadow: inset 0 0 8px rgba(0, 0, 0, 0.5);
  padding: 0 10px;
}
.search-box #search {
  width: 25%;
  height: 100%;
  border: none;
  outline: none;
  background: #e9e522;
  color: #184063;
  font-family: 'Open Sans', sans-serif;
  font-size: 1.2rem;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.5);
}

/**************** Player.js styles **************************/

.player-details {
  color: #b8b8b8;
  font-family: 'Montserrat', sans-serif;
  width: 100%;
  min-height: 100vh;
  padding: 4rem 8rem;
  background: none;
}
/* .player-details-container{

} */

.header {
  width: 95%;
  height: 10vh;
  /* background: #03ef94; */
  display: flex;
  justify-content: space-between;
}
.header > * {
  width: 33%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header .left > * {
  margin: 0 1em;
}
i {
  margin-right: 4px;
}

.main {
  width: 100%;
  /* background: #b8b8b8; */
  height: 70vh;
  color: aliceblue;
  display: flex;
}
.main > * {
  width: 50%;
  padding: 1em;
}
.main .player-stats {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.main .player-stats .head span {
  display: inline-block;
  padding: 1em 2em;
  background: #28c907;
  margin: 0 1em;
  border-radius: 20px;
}
.main .player-stats .sub-stats {
  padding: 0 1em;
  height: 70%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.main .player-stats .sub-stats p {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.main .player-stats .sub-stats p span {
  width: 50%;
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 1100px) {
  .player-details {
    color: #b8b8b8;
    font-family: 'Montserrat', sans-serif;
    width: 100%;
    height: unset;
    padding: 4rem 8rem;
    background-image: linear-gradient(145deg, #03e9f4, #03ef94);
    background-color: #151515;
    background-blend-mode: soft-light;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }
  .player-details-container{
  
  }
  .player-details {
    color: #b8b8b8;
    font-family: 'Montserrat', sans-serif;
    width: 100%;
    height: 100vh;
    padding: 2rem 1rem;
  }
  .main {
    width: 100%;
    /* background: #b8b8b8; */
    height: 70vh;
    color: aliceblue;
    display: flex;
    flex-direction: column;
  }
  .main > * {
    width: 100%;
    padding: 1em 0;
  }

  .header {
    width: 100%;
    height: 10vh;
    display: flex;
    justify-content: space-between;
  }
  .header .right {
    display: none;
  }
  .header > * {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  i {
    margin-right: 4px;
  }
  
.header .left > * {
  margin: 0 .2em;
}
}

/********************* PlayerList styles ***********************/
.player-list {
  width: 100%;
}

.player-list h2 {
  width: 100%;
  background: yellow;
  padding: 0.2em 1em;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
}
.player-list h2 span {
  display: inline-block;
  position: absolute;
  right: 20px;
}
